import React from "react"
import PropTypes from "prop-types"
import styled from "styled-components"
import Label from "@atoms/label"
import {marginZero} from "@breakpoints"


const propTypes = {
  
}





const TagsComponent = ({tags = [], text=[], fill, className, prefix, suffix, ...rest}) => {

 
  return <div className={className}>
    {prefix}
    {tags.map((v,i) => {
      return <Label {...rest} fill={fill ? "true" : undefined} key={`${v}-${i}`} text={text[i]}>{v}</Label>
    })}
    {suffix}
  </div>
  
}

const Tags = styled(TagsComponent)`
  display: flex;
  ${({wrap}) => {
    if(wrap === "left") {
      return `
        flex-wrap: wrap;
        margin: -4px;
        align-items: center;
      `
    } else {
      return `
        flex-wrap: wrap;
        margin: -4px;
        align-items: center;
        justify-content: center;
      `
    }
  }}

  ${({fill}) => {
    if(fill) {
      return `
        width: ${fill};
        flex-direction: column;
      
      `
    }
  }}

  ${Label} {
    margin: 4px;

  }

  margin: ${props => marginZero(props)}
  
`



Tags.propTypes = propTypes
export default Tags