import React from "react"
import PropTypes from "prop-types"
import styled from "styled-components"



const IconComponent = ({size = 1, width, height, children, className, ...rest}) => {
  width = width * size + "rem";
  height = height * size + "rem";
  
  const childrenWithProps = React.Children.map(children, child => {
      // checking isValidElement is the safe way and avoids a typescript error too
    
    if (React.isValidElement(child)) {
        return React.cloneElement(child, {width, height});
    }
    return child;
  });

  return (<div className={className} {...rest} >{childrenWithProps}</div>)
}

export const Icon = styled(IconComponent)`
  display: flex;
  align-items: center;
  justify-content: center;

  svg, path {
    fill: inherit;
  }
`



