import React from "react"
import PropTypes from "prop-types"
import Card from '@atoms/card';
import Heading from '@particles/heading';
import Text from '@particles/text'
import ServiceLogo from "@atoms/service-logo";

const propTypes = {
  
}

const ServiceCard = ({header, children, subtitle, ...rest}) => {
  var type = {facebook: rest.facebook}; 
  var text = "";
  if(type.facebook) {
    type.facebook = "true"
    text = "Facebook"
  } else {
    type.facebook = "true"
    text = "Facebooks"
  }

  return  (
    <Card {...rest} hover={"true"}>
      <Card.Center>
        <ServiceLogo sm {...type} />
        <Heading h4 style={{marginTop: "1rem"}}>{text}</Heading>
        <Text>{subtitle}</Text>
      </Card.Center>
    </Card>
  )
  
}

ServiceCard.propTypes = propTypes
export default ServiceCard