import React from "react"
import PropTypes from "prop-types"
import styled from "styled-components"
import { getColor } from "../styles/helper"
import {marginZero} from "@breakpoints"




const propTypes = {
  as: PropTypes.oneOf(["p","blockquote","span","div","label","li"]),
}


const Text = styled.p.attrs(props => {
  let weight = props.theme.typography.body.fontWeight;
  if(props.bold) weight = 700;
  if(props.medium) weight = 500;
  return {
    as: props.as || "p",
    color: props.theme.palette.text[props.color] || getColor(props),
    weight: weight,
  }
})`
  color: ${props => props.color};
  font-weight: ${props => props.weight};
  font-family: ${props => props.theme.typography.body.fontFamily};
  font-size: ${props => props.size || props.theme.typography.body.fontSize}em;
  line-height: ${props => props.as == "p" ? "1.75rem" : undefined};
  text-transform: ${props => props.theme.typography.body.case || "none"};
  letter-spacing: ${props => props.theme.typography.body.spacing}em;
  margin: ${props => marginZero(props)};
`



Text.propTypes = propTypes
export default Text

