import React from "react"
import PropTypes from "prop-types"
import styled from "styled-components"
import { getColor, getBackgroundColor, shadeColor } from "../../styles/helper"
import Text from "../../particles/text"


const propTypes = {
  
}

const colors = {
  onHover: "#D7DCEA",
  onClick: "#E8FDE2",
  onClickText: "#014C33"
}
 





const LabelComponent = ({children, onClick, text, className, passAs, as, solid, outlined, ...rest}) => {
  return <div className={className}  as={passAs || "label"}  onClick={onClick} {...rest}>{children} <div>{text}</div></div>
}

const Label = styled(LabelComponent)`
  padding: 0.75rem 1.5rem;
  margin: 0.375rem 0.75rem;
  display: flex;
  justify-content: space-between;
  background: ${props => props.solid ? props.theme.colors.surface : ""};
  box-sizing: border-box;

  color: ${({solid, theme}) => solid ? theme.palette.text["200"] : theme.palette.text["300"]};
  font-size: 0.75rem;
  font-weight: 500;
  border-radius: 1.5rem; 

  width: ${props => props.fill ? "100%" : ""};

  border: ${props => props.outlined ? "1px solid #D7DCEA" : undefined};

  user-select: none;

  transition: background 150ms ease-out;
  :hover {
    background: ${colors.onHover};
  }

  :active {
    background: ${colors.onClick};
    color: ${colors.onClickText}; 
  }

`

Label.propTypes = propTypes
export default Label