import React from "react"
import PropTypes from "prop-types"
import styled from "styled-components"
import { getColor, getLevel } from "../styles/helper"
import { marginZero } from "@breakpoints"


const propTypes = {
  as: PropTypes.oneOf(["h1","h2","h3","h4","div"]),
}


const Heading = styled.h1.attrs(props => {
  props.palette = props.palette || "text"
  return {
    level: props.level || getLevel(props),
    as: props.as || getLevel(props),
    color:  props.theme.palette.text[props.color] || props.theme.palette[props.palette][props.color] || getColor(props),
  }
})`
  color: ${props => props.color};
  font-weight: ${props => props.theme.typography[props.level].fontWeight};
  font-family: ${props => props.theme.typography[props.level].fontFamily};
  font-size: ${props => props.theme.typography[props.level].fontSize}em;
  text-transform: ${props => props.theme.typography[props.level].case || "none"};
  letter-spacing: ${props => props.theme.typography[props.level].spacing}em;
  margin: ${props => marginZero(props)};
`



Heading.propTypes = propTypes
export default Heading

