import React from "react"
import PropTypes from "prop-types"
import styled from "styled-components"
import Heading from "@particles/heading"
import ServiceLogo from "@atoms/service-logo"
import Link from "@atoms/link"
import {Icon} from "@icons"
import {marginZero} from "@breakpoints"

const propTypes = {
  
}


const CardHeader = styled(Heading).attrs((props) => {
  props.level = "h4";
})`
  margin-bottom: 1rem;
`

const CardBody = styled.div`
  display: flex;
  flex-direction: column;
  flex: 1;
`

const CardList = styled.ol`
  display: flex;
  flex-direction: column;
  flex: 1;
  margin: 0 -1rem;
  list-style: none;
  padding: 0;
`

const CardFooter = styled.div`
  display: flex;
  margin-top: 1rem;

  ${Link} {
    color: ${props => props.theme.palette.text["200"]};
    display: flex;
    justify-content: space-between;
    width: 100%;

    ${Icon} {
      fill: ${props => props.theme.palette.text["200"]};
    }

    :hover {
      color: ${props => props.theme.palette.primary["300"]};
      ${Icon} {
        fill: ${props => props.theme.palette.primary["300"]};

      }
    }
  }
`

const CardCenter = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
`

const CardListItemComponent = ({children, className, logo, action = () => {}, ...rest}) => {
  return <li className={className} {...rest} onClick={action}>{logo}<div>{children}</div></li>
}

const CardListItem = styled(CardListItemComponent)`
  display: flex;
  align-items: center;

  height: 4rem;

  width: 100%;
  padding: 0 1rem;

  transition: background 150ms ease-out;
  cursor: pointer;
  :hover {
    background: ${props => props.theme.palette.background["200"]};
  }

  div:last-child {
    flex: 1;
    margin-left: ${props => props.logo != undefined ? "0.5rem" : undefined};
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

`



const CardComponent = ({children, className, list, auto, ...rest}) => {

  return <div className={className} {...rest}>{children}</div>
  
}

const Card = styled(CardComponent)`
  display: flex;
  flex-direction: column;
  border: 1px solid #D7DCEA;
  box-sizing: border-box;
  border-radius: 1.5em;
  min-height: ${props => props.list ? "auto" : "240px"};
  width: ${props => props.auto ? undefined : props.width || "300px"};
  padding: 1em;
  margin: ${props => marginZero(props)};

  ${props => {
    if(props.hover) {
      return `
        transition: box-shadow 150ms ease-out;
        :hover {
          box-shadow: 0px 4px 32px rgba(0, 0, 0, 0.08);
        }
      `
    }
  }}
`



Card.propTypes = propTypes
Card.Body = CardBody;
Card.Header = CardHeader;
Card.Footer = CardFooter;
Card.Center = CardCenter;
Card.List = CardList;
Card.List.Item = CardListItem;
export default Card