import { Theme } from "./theme"



export const getColor = (props) => {
  if(props.background) return props.theme.colors.onBackground
  if(props.surface) return props.theme.colors.onSurface
  if(props.primary) return props.theme.colors.onPrimary
  if(props.secondary) return props.theme.colors.onSecondary
  if(props.error) return props.theme.colors.onError
  return props.theme.colors.onBackground;
}

export const getBackgroundColor = (props) => {
  if(props.background) return props.theme.colors.background
  if(props.surface) return props.theme.colors.surface
  if(props.primary) return props.theme.colors.primary
  if(props.secondary) return props.theme.colors.secondary
  if(props.error) return props.theme.colors.error
  return props.theme.colors.primary
}

export const getLevel = (props) => {
  if(props.h1) return "h1";
  if(props.h2) return "h2";
  if(props.h3) return "h3";
  if(props.h4) return "h4";
  return "h1"
}

export const divByRem = (px) => {
  return px / Theme.typography.global.rem;
}

export function shadeColor(colorCode, amount) {
  var usePound = false;

  if (colorCode[0] == "#") {
      colorCode = colorCode.slice(1);
      usePound = true;
  }

  var num = parseInt(colorCode, 16);

  var r = (num >> 16) + amount;

  if (r > 255) {
      r = 255;
  } else if (r < 0) {
      r = 0;
  }

  var b = ((num >> 8) & 0x00FF) + amount;

  if (b > 255) {
      b = 255;
  } else if (b < 0) {
      b = 0;
  }

  var g = (num & 0x0000FF) + amount;

  if (g > 255) {
      g = 255;
  } else if (g < 0) {
      g = 0;
  }

  return (usePound ? "#" : "") + (g | (b << 8) | (r << 16)).toString(16);
}

export function shade(hex, percent = 100) {
  if(hex.charAt(0) == "#") hex = hex.substring(1);
  let r = hex[0] + hex[1];
  let g = hex[2] + hex[3];
  let b = hex[4] + hex[5];
  r = parseInt(r, 16)
  g = parseInt(g, 16)
  b = parseInt(b, 16)
  let max = Math.max(r,g);
  max = Math.max(max, b);

  let hexPercent = (percent * max) / 100;
  hexPercent = Math.floor(hexPercent)
  let hexVal = parseInt(hexPercent, 16);


  r = r - hexVal
  if(r <= 0x00) r = "00";
  else if(r < 0x10) r = "0" + r.toString(16)
  else r = r.toString(16)

  g = g - hexVal
  if(g <= 0x00) g = "00";
  else if(g < 0x10) g = "0" + g.toString(16)
  else g = g.toString(16)

  b = b - hexVal
  if(b <= 0x00) b = "00"; 
  else if(b < 0x10) b = "0" + b.toString(16)
  else b = b.toString(16)

  return "#" + r + g + b;
}

