import React from "react"
import PropTypes from "prop-types"
import styled from "styled-components"
import {Icon} from "@icons"


const propTypes = {
  size: PropTypes.number,
  text: PropTypes.string,
  action: PropTypes.func.isRequired,
  icon: PropTypes.node,
  background: PropTypes.string,
}
const defaultProps = {
  size: 32
}
const StyledIconButton = styled.button`
  width: ${props => props.size}px;
  height: ${props => props.size}px;
  display: flex;
  background: ${props => props.theme.colors.block};
  border-radius: 100%;
  position: relative;
  left: 0;
  top: 0;
  outline: none;
  border: none;
  padding: 0;
  transition: 0.4s;
  cursor: pointer;
  margin-right: 10px;
  align-items: center;
  justify-content: center;
  -webkit-transform: scale(0.8);
  transform: scale(0.8);

  

  div::before, div::after {
    background-color: ${props => props.theme.colors.primary};
  }

  :hover {
    background-color: ${props => props.theme.palette.primary["200"]};
    box-shadow: 0 0 0 5px ${props => props.theme.palette.primary["100"]};
    -webkit-transform: scale(0.8);
    transform: scale(0.8);

    & + div > span {
      transform: translateX(0);
    }

    div::before, div::after {
      background-color: ${props => props.theme.palette.primary["500"]};
    }
  }

  :active {
    background-color: ${props => props.theme.palette.primary["300"]};
    box-shadow: 0 0 0 5px ${props => props.theme.palette.primary["200"]};
    div::before, div::after {
      background-color: white;
    }
  }

  
`

const StyledContainer = styled.div`
  display: flex;
  align-items: center;
`

const StyledAdditionalTextDiv = styled.div`
  overflow: hidden;
`
const StyledAdditionalText = styled.span`
  display: inline-block;
  color: ${props => props.theme.colors.primary};
  font-size: 0.9rem;
  -webkit-transform: translateX(-100%);
  transform: translateX(-100%);
  transition: all 0.3s ease;
`

const IconButton = ({ size, action, text, icon, background}) => (
  <StyledContainer>
    <StyledIconButton size={size} background={background} onClick={action}>
      {icon}
    </StyledIconButton>
    {text && 
      <StyledAdditionalTextDiv>
        <StyledAdditionalText>{text}</StyledAdditionalText>
      </StyledAdditionalTextDiv>
    }
    
  </StyledContainer>
  
)

IconButton.propTypes = propTypes
IconButton.defaultProps = defaultProps
export default IconButton