import React from "react"
import PropTypes from "prop-types"
import styled from "styled-components"
import facebookSVG from "../../../assets/facebook.svg";


const propTypes = {
  
}


const StyledServiceLogo = styled.div`
  display: flex;
  ${({size}) => {
    if(size === "sm") {
      return `
        width: 64px;
        height: 64px;
      `
    } else if(size === "xs") {
      return `
        width: 32px;
        height: 32px;
      `
    } else {
      return `
        width: 125px;
        height: 125px;
      `
    }
  }}
  background: ${props => props.theme.colors.surface};
  border-radius: 100%;
  justify-content: center;
  align-items: center;
  position: relative;

  ::after {
    content: ${props => `"${props.text}"`};
    position: absolute;
    font-size: 40px;
    left: calc(100% + 10px);
    top: 50%;
    transform: translateY(-50%)
  }
`

const StyledImg = styled.img`
  ${({size}) => {
    if(size === "sm") {
      return `
        width: 32px;
        height: 32px;
      `
    } else if(size === "xs") {
      return `
        width: 16px;
        height: 16px;
      `
    } else {
      return `
        width: 65px;
        height: 65px;
      `
    }
  }}
`



const ServiceLogo = ({facebook = true, text = false, lg, sm, xs}) => {

  let src, displayText, size;
  if(facebook) {
    src = facebookSVG
    displayText = "Facebook"
  }

  if(lg) size="lg"
  else if(sm) size="sm"
  else if(xs) size="xs"
  else size = "lg"
  return <StyledServiceLogo size={size} text={text ? displayText : ""}><StyledImg size={size} src={src}/></StyledServiceLogo>
  
}

ServiceLogo.propTypes = propTypes
export default ServiceLogo