import React from "react"
import PropTypes, { resetWarningCache } from "prop-types"
import styled from "styled-components"
import { getColor } from "../../styles/helper"
import { Link as GatsbyLink } from "gatsby";
import {marginZero} from "@breakpoints"



const propTypes = {
  to: PropTypes.string,
  href: PropTypes.string,
  click: PropTypes.func
}



const LinkComponent = ({to , href, children, click, className, ...rest}) => {
  if(to) {
    return <GatsbyLink className={className} to={to}>{children}</GatsbyLink>
  } else if(href) {
    return <GatsbyLink className={className} as="a" href={href}>{children}</GatsbyLink>
  } else if(click) {
    return <GatsbyLink className={className} as="a" onClick={click}>{children}</GatsbyLink>
  }
}

const Link = styled(LinkComponent)`
  color: ${props => props.theme.colors.primary};
  text-decoration: none;
  transition: all 300ms ease-out;
  margin: ${props => marginZero(props)};
  :hover {
    color: ${props => props.theme.palette.primary["500"]};
    cursor: pointer;
  }
`








Link.propTypes = propTypes
export default Link