import React, { useState, useRef } from "react"
import PropTypes from "prop-types"
import styled from "styled-components"
import { useIsMobile, Mobile, Desktop, breakpoints, margin, marginZero, padding} from "@breakpoints"
import Header from "@organisms/header"
import Footer from "@organisms/footer"


const propTypes = {
  
}

const LayoutCenter = styled.div`
  display: flex;
  align-items: center;
  flex-direction: column;
  margin: ${props => margin(props)};

`

const LayoutRow = styled.div`
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: ${props => props.center ? "center" : undefined};
  margin: ${props => margin(props)};

`

const LayoutColumn = styled.div`
  display: flex;
  flex-direction: column;
  align-items: ${props => {
    if(props.center) {
      return "center"
    } else if(props.right) {
      return "flex-end"
    } else {
      return "flex-start"
    }
  }};
  margin: ${props => margin(props)};
  padding: ${props => padding(props)}

`

const LayoutGrid = styled.div`
  display: grid;
  grid-template-columns: ${props => {
    if(props.gridTemplate != undefined) {
      return props.gridTemplate
    } else if(props.fixed != undefined) {
      return `repeat(auto-fit, ${props.fixed}px)`
    } else if(props.min != undefined) {
      return `repeat(auto-fit, minmax(${props.min}px, 1fr))`
    } else {
      return "1fr"
    }
  }};
  grid-gap: ${props => props.gap || 0}rem;
  max-width: ${breakpoints.xl}px;
  margin: ${props => margin(props)};
  width: ${props => props.full ? "100%" : undefined};
`




const LayoutComponent = ({className, siteTitle = "",children, ...rest}) => {

  return (
    <div className={className}>
      <Header/>
      <main style={{width: "100%", maxWidth: "1140px"}}>{children}</main>
      <Footer/>
    </div>
  )
}

const Layout = styled(LayoutComponent)`
  align-items: center;
  display: flex;
  width: 100%;
  flex-direction: column;
  
`


Layout.propTypes = propTypes
Layout.Center = LayoutCenter
Layout.Row = LayoutRow
Layout.Grid = LayoutGrid
Layout.Column = LayoutColumn
export default Layout