import React, { useState, useRef, useEffect } from "react"
import PropTypes from "prop-types"
import styled from "styled-components"
import { IconSearch, IconArrowLeft } from "@icons"
import { useIsMobile, Mobile, Desktop } from "@breakpoints"
import Text from "@particles/text";
import { useComponentVisible } from "@particles/hooks"


const propTypes = {
  
}


const SearchBarInputContainer = styled.div`
  display: flex;
  background: ${props => props.theme.colors.surface};
  padding: ${props => props.nav ? "1em" : "1.25em 1.5em"};
  border-radius: 1.5em;
  align-items: center;

  transition: background 150ms ease-out;

  :hover {
    background: ${props => props.theme.palette.background["300"]};
    cursor: pointer;
  }

  ${IconSearch} {
    height: 1.1875em;
  }

  ${Mobile} {
    width: 100%;
  }

  

`

const SearchBarInput = styled.input`

  width: ${props => props.nav ? 15.625 : 22.5}em;
  background: transparent;
  border: none;
  outline: none;
  color: ${props => props.theme.palette.text['200']};
  font-size: ${props => props.nav ? 0.875 : 1}em;
  font-family: ${props => props.theme.typography.input.fontFamily};
  line-height: 1.1875em;
  margin-right: 0.625em;
  padding: 0;

  :hover {
    cursor: pointer;
  }

  ${Mobile} {
    width: auto;
    flex: 1;
  }
`
const SearchMobileInputContainer = styled(SearchBarInputContainer)`

  border-radius: 0;
  box-shadow: 0 8px 16px rgba(0,0,0,0.1);
  position: sticky;
  top: 0;
  
  :hover {
    background: ${props => props.theme.palette.background["200"]};
  }

  ${SearchBarInput} {
    margin-left: 1.5em;
  }

  ${IconArrowLeft} {
    position: relative;
    ::after {
      content: "";
      width: 2.5em;
      height: 2.5em;
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);

    }
  }
`

const SearchMobileContainer = styled.div`
  display: flex;
  flex-direction: column;
  background: white;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  transform: ${({focused}) => focused ? "translateY(0)" : "translateY(100vh)"};
  transition: transform 200ms ease-out;
  overflow-y: scroll;

`

const Container = styled.div`
  dislay: flex;
  flex-direction: column;
  width: auto;
  position: relative;
  ${Mobile} {
    width: 100%;
  }

`







const SearchResultItemComponent = ({className, title = "", ...rest}) => {

  return (
    <li className={className}><Text color={"200"} as="div">{title}</Text></li>
  )
}

const SearchResultItem = styled(SearchResultItemComponent)`

  transition: background 150ms ease-out;
  cursor: pointer;
 
  :hover {
    background: ${props => props.theme.palette.background['300']};
  }

  :last-child {
    border-radius: 0 0 1.5em 1.5em;
  }

  ${Mobile} {
    :last-child {
      border-radius: 0;
    }
  }

  
  
`

const SearchResultGroupComponent = ({className, title = "", children}) => {

  return (
    <div className={className}>
      <Text bold color={"200"}>{title}</Text>
      <ul>
        {children}
      </ul>
    </div>
  )
}

const SearchResultGroup = styled(SearchResultGroupComponent)`

  dislay: flex;
  margin-top: 1em;

  background: ${props => props.theme.colors.surface};
  border-radius: 1.5em;
  

  ${Mobile} {
    border-radius: 0;
  }


  ${Text} {
    padding: 0.75em 1.5em;
    pointer-events: none;

  }
  
  ul {
    margin: 0;
    padding: 0;
    list-style-type: none;
  }
`

const SearchResultContainer = styled.div`
  position: absolute;
  width: 100%;
  transform: ${props => props.focused ? "scaleY(1)" : "scaleY(0)"};
  transition: transform 100ms ease-in;
  transform-origin: top center;
  z-index: 5;
`





const SearchBar = ({nav = false, placeholder}) => {
  const _isMobile = useIsMobile();
  const mobileInput = useRef(null);
  const desktopInput = useRef(null);
  const [focused, setFocused] = useState(false);
  const {
    ref: resultRef,
    isComponentVisible: isResultVisible,
    setIsComponentVisible: setIsResultVisible
  } = useComponentVisible(false);

  // useEffect(() => {
  //   if(!isComponentVisible) {

  //   }
  // }, [isComponentVisible])

  

  const focusInput = () => {
    

    if(_isMobile) {
      mobileInput.current.focus();
      if(!focused) {
        setFocused(true);
      }
    } else {
      desktopInput.current.focus();
    }
    
    setIsResultVisible(true);

  };

  const blurInput = (e) => {
    setFocused(false);
  }

  const renderResults = () => {
    return (
      <>
        <SearchResultGroup title={"Metrics"}>
          <SearchResultItem title={"MRR Growth Rate"}/>
          <SearchResultItem title={"Monthly Sales"}/>
          <SearchResultItem title={"Marketing Budget"}/>
        </SearchResultGroup>
        <SearchResultGroup title={"Metrics Comparison"}>
          <SearchResultItem title={"MRR vs. ARR"}/>
          <SearchResultItem title={"MRR vs. Revenue"}/>
          <SearchResultItem title={"Batman vs. Superman"}/>
        </SearchResultGroup>
      </>
    )
  }

  return (
    <>
      <Container focused={focused}>
        <SearchBarInputContainer nav={nav} onClick={focusInput}>
          <SearchBarInput nav={nav} placeholder={placeholder} ref={desktopInput}/>
          <IconSearch/>
        </SearchBarInputContainer>
        {!_isMobile && 
          <SearchResultContainer ref={resultRef} focused={isResultVisible}>
            {renderResults()}
          </SearchResultContainer>
        }
      </Container>
      {_isMobile && 
        <SearchMobileContainer focused={focused} onClick={focusInput}>
          <SearchMobileInputContainer >
            <IconArrowLeft onClick={blurInput}/>
            <SearchBarInput placeholder={placeholder} ref={mobileInput}/>
          </SearchMobileInputContainer>
          {renderResults()}
        </SearchMobileContainer>
      }
    </>
  )
}

SearchBar.propTypes = propTypes
export default SearchBar